import React, { Component } from 'react';
import Dropzone from 'react-dropzone';
import { IoIosImage } from 'react-icons/io';
import {Button} from 'reactstrap';

export default class FileUploadForm extends Component {
  state = {
    img : ''
  }

  onDrop = file => {
    let f = file[0];  
    const reader = new FileReader();
    reader.onload = ev => {
      const img = ev.target.result;
      this.setState({ img }, () => this.props.onSelect({ name : this.props.file, data : img }));
    }

    reader.readAsDataURL(f);
  };

  cancel = () => {
    this.setState({ img : '' }, () => {
      this.props.onCancel(this.props.file);
    });
  }

  reject = err => console.log(err)

  render() {
    return (
      <div className="col-md-3">
        { this.state.img !== '' &&
          <div className="card border-0 shadow">
            <div className="card-body">
              <Button block color="danger" onClick={this.cancel}>Batal</Button>
            </div>
            <img src={this.state.img} alt="" className="card-img"/>
          </div>
        }
        { this.state.img === '' && <Dropzone accept={['image/jpg', 'image/jpeg', 'image/png']} onDropRejected={this.reject} onDrop={this.onDrop}>
          {({ getRootProps, getInputProps }) => (
            <div className="card my-2" style={{ cursor : 'pointer' }} {...getRootProps()}>
              <div className="card-body text-center">
                <input {...getInputProps()}/>
                <p className={`${this.props.required ? 'text-danger' : 'text-success'} m-0`}>{this.props.required ? 'Harus di-upload' : 'File Opsional'}</p>
                <IoIosImage style={{ fontSize : 80 }} className="text-dark" />
                <p className="m-0">Seret file {this.props.file} di sini, atau klik di sini untuk memilih file</p>
                <p className="text-muted m-0 small">Format : jpg, jpeg, png</p>
              </div>
            </div>
          )}
        </Dropzone>}
      </div>
    )
  }
}
