import React, { Component } from 'react'
import { Card, CardBody, CardImg, Input } from 'reactstrap';

export default class Captcha extends Component {
  state = {
    captcha: ''
  }

  onChange = ev => {
    // console.dir(ev.target.value);
    this.setState({ captcha : ev.target.value }, () => this.props.onChange(this.state.captcha));
  }

  render() {
    return (
      !this.props.submitting && <div className="row">
        <div className="col-md-3">
          <Card>
            <CardImg top src={`${process.env.REACT_APP_IP_ADDRESS}:${process.env.REACT_APP_PORT}/captcha`} />
            <CardBody>
              <Input disabled={this.props.error} invalid={this.props.invalid} name="captcha" onChange={this.onChange} value={this.state.captcha} placeholder="Masukkan huruf seperti gambar di atas" size="sm" />
            </CardBody>
          </Card>
        </div>
      </div>
    )
  }
}
