import React, { Component } from 'react';
import { Modal, ModalBody, ModalHeader, Button, Col, Row, Table } from 'reactstrap';
import moment from 'moment';
import 'moment/locale/id';

moment.locale('id');
export default class ConfirmRegister extends Component {
  state = {
    open: false
  }

  open = () => this.setState({ open: true });

  close = () => this.setState({ open: false });

  render() {
    return (
      <>
        <Modal isOpen={this.state.open}>
          <ModalHeader color="danger">Konfirmasi Pendaftaran</ModalHeader>
          <ModalBody>
            <h4 className="m-0 text-center">Apakah Anda yakin mendaftar dengan data sebagai berikut</h4>
          </ModalBody>
            <Table>
              <tbody>
                <tr>
                  <td>Nama</td>
                  <td>{this.props.name}</td>
                </tr>
                <tr>
                  <td>Tujuan</td>
                  <td>{this.props.purpose}</td>
                </tr>
                <tr>
                  <td>Tanggal</td>
                  <td>{moment(this.props.date[0]).format('DD MMMM YYYY')}</td>
                </tr>
                <tr>
                  <td>Jam</td>
                  <td>{this.props.time}</td>
                </tr>
              </tbody>
            </Table>
          <ModalBody>
            <Row noGutters>
              <Col md="6" className="px-1">
                <Button onClick={() => this.setState({ open: false })} block color="light">Tidak</Button>
              </Col>
              <Col md="6" className="px-1">
                <Button onClick={() => this.props.onClick()} block color="success">Ya</Button>
              </Col>
            </Row>
          </ModalBody>
        </Modal>
      </>
    )
  }
}
